import React from "react";

function Food({ fav, num }) {
  return (
    <h1>I like {fav} {num} time{num > 1 ? "s" : ""}</h1>
  );
}

function App() {
  return (
    <div>
      <h1>Hello</h1>
      <p>React App is good</p>
      <Food fav="kimchi" num={1} />
      <Food fav="ramen" num={2}/>
      <Food fav="chukumi" num={5}/>
      <Food fav="bread" num={4}/>
    </div>
  );
}

export default App;